nav{
    background-color: rgba(0, 0,0, 0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}
nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.5rem;
}
nav a:hover{
    background-color: rgba(0, 0,0, 0.3);
}
nav a.active{
    background-color: var(--color-primary);
    color: var(--color-white);
}

/* media query */
@media screen and (max-width: 600px) {
    nav{
        padding: 0.2rem 1rem;
        gap: 0.2rem;
    }
    nav{
        padding: 0.2rem 1rem;
        gap: 0.2rem;
    }
  }
  @media screen and (max-width: 415px){
    nav a{
        font-size: 1.2rem;
        padding: 0.7rem;
    } 
  }

  @media screen and (max-width: 391px){
    nav a{
        font-size: 1rem;
        padding: 0.5rem;
    }
   
  }